<template>
  <!-- <li class="catalog__item">
    <img
      v-if="product.images.length"
      :src="product.images[0].url || ''"
      :alt="product.title"
    />
    <h3 class="catalog__title">
      <a @click.prevent=""> {{ product.name }} </a>
    </h3>

    <ul class="catalog__group">
      <li class="item" v-for="item in product.volumes" :key="item.id">
        <span class="catalog__price"> {{ item.price }} ₽ </span>
        <span v-if="item.subprice" class="catalog__subprice">
          {{ item.subprice[0].value }} ₽</span
        >
        <span class="catalog__volume"> {{ item.volume }} </span>
      </li>
    </ul>
    <h2 class="catalog__title">
      <a @click.prevent="openModal('change')"> Изменить </a>
    </h2>

    <DeleteButton @click="openModal('delete')"></DeleteButton>

  </li> -->
  <tr class="uk-text-middle" :key="product.id">
    <td>
      <span class="data__name">{{ product.name }} </span>
    </td>
    <td>
      <div class="uk-flex uk-flex-left uk-flex-stretch data-list">
        <div
          v-for="item in product.volumes"
          :key="item.id"
          class="uk-flex uk-flex-column uk-flex-around data__volume-item"
        >
          <span class="uk-text-center">
            <input class="data__price" type="number" v-model="item.price" />
            ₽
          </span>
          <span v-if="item.subprice" class="uk-text-center"
            ><input
              class="data__price data__subprice"
              type="number"
              v-model="item.subprice[0].value"
            />
            ₽</span
          >
          <small class="data__volume uk-text-meta uk-text-center"
            >{{ item.volume }}
          </small>
          <div class="counter">
            <a
              href=""
              @click.prevent="item.quantity ? (item.quantity -= 1) : 1"
              uk-icon="icon: minus-circle"
              class="uk-icon-button"
            ></a>

            <input
              class="counter__input uk-text-center uk-form-small"
              type="number"
              v-model="item.quantity"
            />
            <a
              href=""
              @click.prevent="item.quantity += 1"
              uk-icon="icon: plus-circle"
              class="uk-icon-button"
            ></a>
          </div>
          <!-- <small v-else class="uk-text-danger uk-text-center">
            Нет в наличии
          </small> -->
        </div>
      </div>
    </td>
    <td>
      <ul class="uk-flex uk-flex-stretch uk-flex-wrap data-list">
        <li
          class="data-list__item"
          v-for="item in product.images"
          :key="item.id"
        >
          <img class="uk-card uk-card-default" :src="item.url" alt="" />
        </li>
      </ul>
    </td>
    <td>
      <span class="uk-text-meta"> {{ formatTime(product.created) }}</span>
    </td>
    <td>
      <span class="uk-text-meta"> {{ formatTime(product.changed) }}</span>
    </td>
    <td style="position: relative">
      <div class="uk-flex uk-flex-column">
        <button
          @click.prevent="openModal('change')"
          class="data__button uk-button uk-button-default uk-label-success"
        >
          Изменить
        </button>
        <button
          class="data__button uk-button uk-button-default uk-label-danger"
          @click="openModal('delete')"
        >
          Удалить
        </button>
        <button
          class="data__button uk-button uk-button-default uk-label-warning"
        >
          Посмотреть
        </button>
      </div>
      <DeleteConfirm
        :text="'Вы уверены, что хотите удалить товар?'"
        v-model:open="isDeleteOpen"
        @yes="deleteItem"
      ></DeleteConfirm>
      <DataCreateProduct
        v-if="isChangeOpen"
        v-model:is-modal-open="isChangeOpen"
        @change="emit('change')"
        :window="'change'"
      ></DataCreateProduct>
    </td>
  </tr>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import useApi from '@/composible/useApi'
import DeleteConfirm from '@/components/DeleteConfirm.vue'
import DeleteButton from '@/components/DeleteButton.vue'
import SubBtn from '@/components/SubBtn.vue'
import { ref, defineProps, defineEmits, watch } from 'vue'
import DataCreateProduct from '@/components/dataBase/DataCreateProduct.vue'
import useHelpers from '@/composible/useHelpers'
import DataProductsVue from '@/views/DataProducts.vue'
const { formatTime } = useHelpers()
const isChangeOpen = ref(false)
const isDeleteOpen = ref(false)
const emit = defineEmits(['change'])
const props = defineProps(['product'])
const openModal = (doing) => {
  if (doing === 'delete') {
    isDeleteOpen.value = true
  } else if (doing === 'change') {
    localStorage.setItem('changeFormData', JSON.stringify(props.product))
    isChangeOpen.value = true
  }
}
const { deleteProduct, patchProduct } = useApi()
const deleteItem = async () => {
  const res = await deleteProduct(props.product.id)
  emit('change')
}

watch(
  () => props.product,
  (value) => {
    patchProduct(value)
  },
  {
    deep: true
  }
)
</script>
<style lang="scss">
.cart__item {
  position: relative;
}

.data-list {
  min-width: 200px;
  flex-wrap: nowrap;
}

.data-list__item {
  height: 60px;
  max-width: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 3px;
  margin-bottom: 5px;
}

.data__volume-item {
  max-width: 100px;
  padding: 0 3px;
  margin-bottom: 5px;
}

.data-block {
  display: block;
  max-width: 50px;
}

@mixin normalizeInput {
  padding: 0 !important;
  display: inline-block;
  border: none;
  outline: none;
  max-width: 36px;
}

.data__price {
  @include normalizeInput;
  text-align: end;
  font-weight: 600;
}

.data__subprice {
  text-decoration: line-through;
}

.data__volume {
  transform: translateX(6px);
}

.data__name {
  max-width: 150px;
  display: inline-block;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
}

.counter {
  display: flex;
  flex-wrap: nowrap;

  &__input {
    @include normalizeInput;
  }
}

@media (max-width: 960px) {
  .uk-table-responsive {
    max-width: 250px;
  }
}
</style>
